.about{display:grid;grid-template-areas:"title title" "photo job" "photo skills" "description description";align-self:center;height:100%;padding:1.25rem 1rem 2.25rem;gap:0 .5rem;max-width:100%;grid-template-rows:auto 1fr auto auto;grid-template-columns:auto 1fr}@media(min-width: 1024px){.about{grid-template-areas:"title title" "photo job" "photo description" "photo skills";padding:2rem 0 2.5rem;gap:0 2rem;max-width:960px}}.about__title{grid-area:title;font-size:3rem;font-weight:bold;text-transform:uppercase;color:var(--support);margin:0;letter-spacing:2pt;margin-bottom:1.25rem}@media(min-width: 1024px){.about__title{margin-bottom:1.5rem;letter-spacing:8pt}}.about__img-container{width:175px;height:188px;grid-area:photo;align-self:end}@media(min-width: 1024px){.about__img-container{width:448px;height:387px}}.about__img{width:100%;height:100%;object-fit:cover;margin-bottom:1.25rem}@media(min-width: 1024px){.about__img{margin-bottom:2rem}}.about__job{grid-area:job;text-transform:uppercase;border-top:2px solid var(--support);padding-top:.5rem;font-size:.875rem;align-self:end;padding-bottom:1.25rem}@media(min-width: 1024px){.about__job{padding-top:.75rem;font-size:1.5rem;padding-bottom:2.25rem}}.about__description{grid-area:description;align-self:end;padding-bottom:0;margin-top:1.25rem}@media(min-width: 1024px){.about__description{padding-bottom:3.75rem;margin-top:0}}.about__skills-list{grid-area:skills;display:flex;gap:.5rem;flex-direction:column;align-self:end;padding-bottom:1.25rem}@media(min-width: 1024px){.about__skills-list{gap:1.25rem;flex-direction:row}}.about__skills-item{display:flex;font-size:.875rem;align-items:center}.about__skills-item::before{content:"";width:.75rem;height:.75rem;border-radius:100%;background-color:var(--support);margin-right:.5rem}@media(min-width: 1024px){.about__skills-item{font-size:2rem}.about__skills-item::before{width:1.25rem;height:1.25rem;margin-right:1.25rem}}